"use client";
import { useState, useRef } from "react";
import { useLoginClient } from "../context/regester/login_context";
import {
  FaUserCircle,
  FaBell,
  FaLock,
  FaWallet,
  FaUser,
  FaQuestionCircle,
} from "react-icons/fa";
import Image from "next/image";

export default function ProfilePage() {
  const { saveData } = useLoginClient();
  const [profileImage, setProfileImage] = useState(null);
  const fileInputRef = useRef(null);

  if (!saveData) return <p className="p-4">Loading...</p>;

  const handleUploadClick = () => {
    fileInputRef.current.click(); // يفتح نافذة اختيار صورة
  };

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      // تقدر تستخدم base64 كمان لو عايز تحفظها لاحقًا
      const profilePicture = URL.createObjectURL(file);
      setProfileImage(profilePicture);
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 text-gray-800">
      {/* Navbar */}
<div className="bg-white shadow px-6 py-4">
  <div className="grid grid-cols-1 lg:grid-cols-3 items-center gap-4">
    {/* 1. Logo */}
    <div className="text-lg font-bold text-green-600">
      logo
    </div>

    {/* 2. Search bar */}
    <div className="flex justify-center gap-3">
          <input
            type="text"
            placeholder="Find the perfect service you need"
            className="px-3 py-2 border rounded-md w-72"
          />
          <button className="bg-green-500 hover:bg-green-600 text-white px-4 py-2 rounded-md">
            Search
          </button>
    </div>

    {/* 3. Right Section */}
    <div className="flex justify-end items-center gap-4">
      <span className="cursor-pointer">Requests</span>
      <span className="cursor-pointer">Inbox</span>
      <div className="relative group">
        <span className="cursor-pointer font-medium">{saveData.displayName}</span>
        <div className="absolute hidden group-hover:block right-0 mt-2 bg-white border rounded-md shadow-md min-w-[120px] z-10">
          <div className="p-2 hover:bg-gray-100 cursor-pointer">Account</div>
          <div className="p-2 hover:bg-gray-100 cursor-pointer">help</div>
          <div className="p-2 hover:bg-gray-100 cursor-pointer">Log out</div>
        </div>
      </div>
    </div>
  </div>
</div>


      {/* Main Section */}
      <div className="flex p-8 gap-6">
        {/* Left - Profile Card */}
        <div className="bg-white rounded-lg shadow w-1/4 text-center py-6 px-4">
          {profileImage ? (
            <Image
            width={20}
            height={20}
              src={profileImage}
              alt="Profile"
              className="w-28 h-28 mx-auto rounded-full object-cover"
            />
          ) : (
            <FaUserCircle className="text-gray-400 mx-auto text-7xl" />
          )}
          <h2 className="text-lg font-semibold mt-4">{saveData.displayName}</h2>
          <p className="text-sm text-gray-500 mb-4">{saveData.email}</p>
          <input
            type="file"
            accept="image/*"
            ref={fileInputRef}
            onChange={handleImageChange}
            className="hidden"
          />
          <button
            onClick={handleUploadClick}
            className="border border-gray-300 px-4 py-2 text-sm rounded-md"
          >
            Upload photo
          </button>
        </div>

        {/* Right - Info Boxes */}
        <div className="flex-1 space-y-4">
          <InfoBox
            icon={<FaUser />}
            title="My profile"
            desc="Edit your profile picture, name, email, phone number, and Location"
          />
          <InfoBox
            icon={<FaWallet />}
            title="Safe"
            desc="Top up your account, check your balance, and manage your payment preferences."
          />
          <InfoBox
            icon={<FaLock />}
            title="Change password"
            desc="Update and manage your password."
          />
          <InfoBox
            icon={<FaBell />}
            title="Notifications"
            desc="Choose notification preferences and how you want to be contacted"
          />
          <InfoBox icon={<FaQuestionCircle />} title="help" desc="Help" />
        </div>
      </div>

      {/* Footer */}
      <footer className="text-sm text-gray-500 text-center py-6 border-t">
        2025 Lilason, Inc. – <a href="#" className="underline">Terms of Use</a> – <a href="#" className="underline">Privacy Policy</a> – <a href="#" className="underline">Accessibility</a> – <a href="#" className="underline">Service Code</a>
      </footer>
    </div>
  );
}

function InfoBox({ icon, title, desc }) {
  return (
    <div className="bg-white p-4 rounded shadow cursor-pointer hover:bg-gray-50">
      <div className="flex justify-between items-start gap-2">
        <div className="flex items-start gap-2">
          <div className="text-xl text-green-600">{icon}</div>
          <div>
            <h3 className="font-semibold">{title}</h3>
            <p className="text-sm text-gray-600">{desc}</p>
          </div>
        </div>
        <span className="text-2xl text-gray-400">›</span>
      </div>
    </div>
  );
}
